$breakpoints: (
  xs: (
    min-width: 360px,
  ),
  sm: (
    min-width: 576px,
  ),
  md: (
    min-width: 768px,
  ),
  lg: (
    min-width: 992px,
  ),
  xl: (
    min-width: 1200px,
  ),
  xxl: (
    min-width: 1440px,
  ),
);

$orientations: (
  l: (
    orientation: landscape,
  ),
  p: (
    orientation: portrait,
  ),
);

$color-text: lighten(#c9d1d9, 10%);
$color-section-bg: rgba(0, 0, 0, 0.2);
$color-item: darken($color-section-bg, 5%);
$color-item-hover: lighten($color-section-bg, 25%);
$color-item-active: darken($color-section-bg, 5%);
$color-scrollbar: darken(#09203f, 20%);

$border-radius: 20px;
$transision: 0.3s;

$sidebar-width: 400px;
$gap: 20px;

$user-panel-padding: 30px;
$user-photo-width: 70px;
$user-settings-width: 30px;

$item-height: 50px;

@keyframes drop {
  from {
    top: -40px;
  }

  to {
    opacity: 1;
  }
}

@mixin media($breakpoint, $orientation: null) {
  $size: map-get($breakpoints, $breakpoint);
  $viewport: map-get($orientations, $orientation);
  @if ($size and $viewport) {
    @media #{$size} and #{$viewport} {
      @content;
    }
  } @else if ($size) {
    @media #{$size} {
      @content;
    }
  } @else {
    @error 'Wrong breakpoint or orientation value';
  }
}

@mixin background {
  background-image: linear-gradient(to top, #09203f 0%, #537895 100%);
}

@mixin box {
  background-color: $color-section-bg;
  border-radius: $border-radius;
  overflow: hidden;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
  backdrop-filter: blur(10px);
}

@mixin list-item {
  display: flex;
  align-items: center;
  padding: 0 20px;
  flex-basis: $item-height;

  &:hover {
    background-color: $color-item-hover;
  }
}

@mixin list-icon {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 30px;
  flex-shrink: 0;
}

@mixin button {
  background: none;
  border: none;
  cursor: pointer;

  &:focus {
    outline: none;
  }
}

@mixin list-in-box {
  display: flex;
  flex-direction: column;
  padding: $border-radius 0;
}

@mixin scrollbar {
  @supports (-moz-appearance: none) {
    scrollbar-color: $color-scrollbar transparent;
    scrollbar-width: thin;
    margin-right: 4px;
    padding-right: 4px;
  }

  &::-webkit-scrollbar {
    width: 16px;
  }

  &::-webkit-scrollbar-thumb {
    border-radius: 30px;
    background-color: $color-scrollbar;
    border-right: 5px solid rgba(0, 0, 0, 0);
    border-left: 5px solid rgba(0, 0, 0, 0);
    background-clip: padding-box;
  }
}

@mixin delay($number, $value) {
  @for $i from 1 to ($number + 1) {
    &:nth-of-type(#{$i}) {
      animation-delay: (#{$i * $value});
    }
  }
}

.add-task {
  position: relative;
  display: flex;
  @include list-item;
  border-radius: 10px;
  background-color: $color-item;
  margin-bottom: 20px;
  flex-shrink: 0;
  margin-right: 16px;
  margin: 20px 16px 20px 0;
  transition: $transision;

  .add-task-icon {
    @include list-icon;
    .add-icon {
      width: 100%;
    }
  }

  #add-task-input {
    padding: 0 10px;
    width: 100%;
    background-color: transparent;
    color: $color-text;
    font-size: 1rem;
    border: none;

    &:focus {
      outline: none;

      &::placeholder {
        color: darken($color-text, 20%);
      }
    }

    &::placeholder {
      color: $color-text;
    }
  }
}
